<template>
  <div class="container">
    <van-nav-bar title="搜索中心" left-arrow @click-left="$router.push('/')" />
    <van-search v-model.trim="q" placeholder="请输入搜索关键词" shape="round" @search="onSearch" />
    <!-- 词条 -->
    <van-cell-group class="suggest-box" v-if="q">
      <van-cell
        icon="search"
        v-for="item in options"
        :key="item"
        @click="onSearch(item.replace(`<span>${q}</span>`,q))"
      >
        <p v-html="item"></p>
      </van-cell>
    </van-cell-group>
    <!-- 历史记录 -->
    <div class="history-box" v-else-if="historyList.length">
      <div class="head">
        <span>历史记录</span>
        <van-icon name="delete" @click="clearHistory"></van-icon>
      </div>
      <van-cell-group>
        <van-cell v-for="item in historyList" :key="item">
          <a class="word_btn" @click="toSearch(item)">{{item}}</a>
          <van-icon class="close_btn" slot="right-icon" name="cross" @click="delHistory(item)" />
        </van-cell>
      </van-cell-group>
    </div>
  </div>
</template>

<script>
import { suggest } from '@/api/articles'
const KEY = 'hm-toutiao-m-84-history-key'
export default {
  name: 'search-index',
  data () {
    return {
      // 搜索关键字  v-model.trim 自动剔除两侧的空字符
      q: '',
      historyList: JSON.parse(window.localStorage.getItem(KEY) || '[]'),
      // 词条
      options: [],
      timer: ''
    }
  },
  watch: {
    q () {
      if (!this.q) return false
      // 优化：(函数防抖)
      // 1. 当你输入内容后，等待一些时间计时，如果用户没有改变内容，此时发送请求
      // 2. 当你输入内容后，在等待的过程中，用户改变了内容，重新计时
      window.clearTimeout(this.timer)
      this.timer = window.setTimeout(async () => {
        const data = await suggest(this.q)
        this.options = data.options.map(item =>
          item.toLowerCase().replace(this.q, `<span>${this.q}</span>`)
        )
      }, 200)
    }
  },
  created () {},
  methods: {
    onSearch (key) {
      if (!key.trim()) return false
      const set = new Set(this.historyList)
      set.add(key)
      // 把set转为数组  Array.from(set) 或者 [...set]
      this.historyList = [...set]
      window.localStorage.setItem(KEY, JSON.stringify(this.historyList))
      this.$router.push({ path: '/search/result', query: { q: key } })
    },
    // 跳转去搜索
    toSearch (key) {
      this.$router.push({ path: '/search/result', query: { q: key } })
    },
    // 删除历史记录
    delHistory (key) {
      this.historyList.splice(
        this.historyList.findIndex(item => item === key),
        1
      )
      window.localStorage.setItem(KEY, JSON.stringify(this.historyList))
    },
    // 清空历史记录
    clearHistory () {
      this.historyList = []
      window.localStorage.setItem(KEY, JSON.stringify(this.historyList))
    }
  }
}
</script>

<style lang="less" scoped>
.history-box {
  padding: 0 20px;
  .head {
    line-height: 36px;
    color: #999;
    .van-icon {
      font-size: 16px;
      float: right;
      margin-top: 10px;
    }
  }
  .van-cell {
    padding: 10px 0;
  }
  .word_btn {
    color: #3296fa;
  }
  .close_btn {
    margin-top: 5px;
    color: #999;
  }
}
.suggest-box {
  /deep/ .van-cell {
    padding: 10px 20px;
    color: #999;
    p {
      span {
        color: red;
      }
    }
  }
}
</style>
